*{
    margin: 0;
    padding: 0;
}
.container{
    width:100vw;
    min-height:100vh;
    background-color:lightgray;
}
.row{
    display:flex;
}
.logo{
    width:10vw;
    margin-left:2vw;
    padding: 20px;
}
.heading{
    width:90vw;
    height:10vh;
    text-align:center;
    padding: 20px;
    box-sizing: border-box;
}
.navbar{
   
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    
    
}
.navbar a{
    margin: 20px;
    box-sizing: border-box;
    
}
.row2{
   
    display: flex;
   
   
}
.sideMenu{
    border: 5px solid rgb(152, 152, 155);
    padding: 40px;
    border-radius: 10px;
    width: 40vw;
    height: 50vh;
    margin: 5PX;
    }

.sideMenu ul {
    padding: 20px;
    cursor: pointer;
}
.mainbox1{
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    margin: 10px;
}
.mainbox2{
    display: flex;
    flex-direction: column;
    margin: 10px;
    box-sizing: border-box;
    margin: 10px;
    }
.box1 img{
    width: 12rem;
    height: 7rem;
}
.box4 img{
    width: 12rem;
    height: 12rem;
}
.box1,.box2, .box3, .box4{
    padding: 10px;
  
  }

.footer{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1px;
}
.about{
    background-color: brown;
    width: 100vw;
    height: 100vh;
}
.contact{
    background-color: rgb(18, 33, 63);
    width: 100vw;
    height: 100vh;
}
.products{
    background-color: rgb(11, 121, 95);
    width: 100vw;
    height: 100vh;
}



@media screen and (max-width:600px){
    .heading h1{
        font-size: 30px;
    }
    .navbar{
   
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        font-size: 20px;
        cursor: pointer;
        
        
    }
    h1{
        font-size: 15px;
    }
    .sideMenu{
        border: 3px solid rgb(152, 152, 155);
        padding: 20px;
        border-radius: 5px;
        width: 60vw;
        height: 20vh;
        margin: 20px;
        }
        .row2{
            display: flex;
            flex-direction: column;
        }
        .box1 img{
            width: 15rem;
            height: 7rem;
            padding: 5px;
        }
        .box4 img{
            width: 15rem;
            height: 12rem;
            padding: 5px;
        }
        p{
            padding: 5px;
        }
        h4{
            padding: 2px;
        }
        .footer{
            padding: 20px;
        }

}

